import React from "react";
import {inject, observer} from "mobx-react";

@inject('userStore')
@observer
export default class UserCreationForm extends React.Component {
  user = {
    username: '',
    password: '',
    email   : ''
  };

  constructor(props) {
    super(props);
    this.state = {
      usernameValue: '',
      passwordValue: '',
      emailValue   : ''
    }

  }

  getUserDetails() {
    return this.state;
  };

  add() {
    this.props.userStore.create(this.getUserDetails());
    alert(this.getUserDetails().usernameValue + this.getUserDetails().passwordValue)
  }

  render() {
    return (
      <div>
        <input type="text" name="username" onChange={this.updateUsernameValue}/>
        <br/>
        <input type="password" name="password" onChange={this.updatePasswordValue}/>
        <br/>
        <input type="email" name="email" onChange={this.updateEmailValue}/>
        <button onClick={() => this.add()}>Добавить пользователя
        </button>
      </div>
    );
  }

  updateUsernameValue(evt) {
    this.setState = {usernameValue: evt.target.value}
  }

  updatePasswordValue(evt) {
    this.setState = {usernameValue: evt.target.value}
  }

  updateEmailValue(evt) {

    this.setState = {usernameValue: evt.target.value}
  }
}


